/* 히스토리 */

.seven{ text-align: center; margin: 40px 0 50px 0;}

.seven h2{
  font-size: 40px; line-height: 55px; font-weight: 400; letter-spacing: -2.34px;
}
.seven h2 b{ color: #01944e;}

.seven div{ width: 840px; position: relative; margin: 0 auto; margin-top: 40px; margin-bottom: 20px;}
.seven div p:nth-of-type(2){ position: absolute; left: 50%; top: 0;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
.seven div p:nth-of-type(1) img{ width: 840px;}
.seven div p:nth-of-type(2) img{ width: 840px;}
  /* 연도 버튼 */
.seven div button{ width: 110px; height: 40px; opacity: 0; cursor: pointer;}
.seven div button:nth-of-type(1){ position: absolute; right: 0; top: 20px; }
.seven div button:nth-of-type(2){ position: absolute; right: 0; top: 75px; }
.seven div button:nth-of-type(3){ position: absolute; right: 0; top: 132px; }
.seven div button:nth-of-type(4){ position: absolute; right: 0; top: 190px; }
.seven div button:nth-of-type(5){ position: absolute; right: 0; top: 245px; }


.seven p{
  display: flex; align-items: center; justify-content: center;
}
.seven p button{ 
  width: 100px; margin: 0 20px;
  background: none; border: none;
  width: 60px; height: 60px;
  cursor: pointer;
}
.seven p button{
  background: url(data:image/png;base64,) center no-repeat;
}
.seven p button:nth-of-type(1){
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.seven p button:hover{
  background: url(data:image/png;base64,) center no-repeat #01944e; border-radius: 30px;
}

.history{
  text-align: center; background: #f1f1f1; padding-top: 70px;
}
.history h2{
  font-size: 40px; line-height: 55px; font-weight: 400; letter-spacing: -2.34px;
}
.history div{ margin: 30px 0 50px 0; }
#day{ width: 200px; height: 20px; text-align: center; background: none;}
#day option{ background: #01944e; color: white;}

.history{ padding-bottom: 50px;}
.history table{
  display:flex; display: -ms-flexbox;
  overflow-x: auto; overflow-y: hidden;
  max-width: 1200px; margin: 0 auto;
}
.history thead{ width: 300px; }
.history thead tr th{ color: #aaa; }
.history thead tr th:nth-of-type(1){ height: 25px; }
.history thead tr th:nth-of-type(2){
  height: 290px; padding: 125px 0; box-sizing: border-box;
}
.history thead tr th:nth-of-type(3){ height: 130px;}
.history tbody{display:flex;}
.history th,td{ display:block; }

.history tbody tr td:nth-of-type(1){ height: 25px; color: #01944e; }
.history tbody tr td:nth-of-type(2){ height: 290px; padding-top: 20px;}
.history tbody tr td:nth-of-type(2) img{ width: 100px; }
.history tbody tr td:nth-of-type(3){ color: #555; padding: 0 10px; font-size: 14px; height: 130px;}


/* 굿즈 */

.intro{
  width: 1280px; margin: 40px auto;
  display: flex; align-items: center; justify-content: center;
}
.intro article p{ margin-top: 14px; font-size: 24px; line-height: 36px; font-weight: 700;}
.intro article h2,.intro article h2 b{ 
  font-size: 50px; line-height: 55px; font-weight: 700; letter-spacing: -2.34px;
  color: #01944e; 
}
.intro article h2 b{ color: #333; }
.intro article div{
  margin-top: 46px; font-size: 17px; line-height: 30px; color: #666;
}
.intro figure{ width: 35%;}
.intro figure img{ width: 100%;}


.goods{ margin: 0 auto; position: relative; min-width: 1280px; overflow-x: hidden; }
.goods ul::before{
  content: ''; display: block; width: 440px; height: 100%;
  position: absolute; left: 0; top: 0;
  background: url(/sevenstar/static/media/goods_pettern_left.99a4b721.jpg) center top repeat-y;
  z-index: -1; -webkit-transform: translateX(-200px); transform: translateX(-200px);
}
.goods ul::after{
  content: ''; display: block; width: 440px; height: 100%;
  position: absolute; right: 0; top: 0;
  background: url(/sevenstar/static/media/goods_pettern_right.88047467.jpg) center top repeat-y;
  z-index: -1; -webkit-transform: translateX(200px); transform: translateX(200px);
}
.goods ul{
  display: flex;  list-style: none; flex-wrap: wrap; 
  width: 1280px; margin: 0 auto; padding: 0;
}
.goods ul li{
  width: 280px; height: 280px; background-color: #f1f1f1; 
  margin: 25px 70px; border-radius: 50%;
}
.goods ul li img{ width: 100%;}
@media (max-width:1480px){
  .goods ul::before, .goods ul::after{ display: none;}
}



