.pop{  width: 100%; height: 100vh;
  background: rgba(0,0,0,0.5);
  position: fixed; left: 0; top: 0;
  z-index: 30;
}
.pop > div{ width: 430px; background: #313131;
  position: relative; padding: 25px;
  position: absolute; left: 50%; top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin: 0 auto;
}
.poptit {
  border: 2px solid #11ac5d; text-align: center;
  position: relative; height: 116px; width: 100%;
  border-radius: 10px;
}
.poptit button{ 
  width: 46px; height: 46px; background: none; border: none;
  cursor: pointer; background: url(data:image/png;base64,) center no-repeat #01944e; border-radius: 30px;
}
.poptit button:nth-of-type(1){ position: absolute; left: -23px; top: 50%; -webkit-transform: translateY(-50%) rotate(180deg); transform: translateY(-50%) rotate(180deg); }
.poptit button:nth-of-type(2){ position: absolute; right: -23px; top: 50%; -webkit-transform: translateY(-50%); transform: translateY(-50%);}
